export default Vue.extend({
  name: 'App',
  components: {
    AboutMe,
    EmailInfo,
    GitHubInfo,
    LinkedInInfo,
    PhoneInfo,
    ThemeChanger,
  },
  data: () => ({
    //
  }),
  computed: {
    theme() {
